import _ from "lodash";

const determineYPosition = (el, list, unions, y) => {
  const shouldSetY = !_.has(el, "y") && !_.isUndefined(y) && !_.isNaN(y);

  if (shouldSetY) el.y = y;

  if (_.has(el, "y")) {
    const listA = list.filter((a) => !_.has(a, "y"));
    const determine = (els, value) => {
      els.forEach((a) => determineYPosition(a, listA, unions, el.y + value));
    };

    const myChildren = listA.filter(({ p }) => p.indexOf(el.id) !== -1);
    const mySiblings = listA.filter(
      ({ p }) => p.length > 0 && _.isEqual(el.p, p)
    );
    const myParents = listA.filter(({ id }) => el.p.indexOf(id) !== -1);
    const mySpouses = listA.filter(({ id }) => {
      return !!unions.find(({ p }) => _.isEqual(p.sort(), [id, el.id].sort()));
    });

    determine(myChildren, 1);
    determine(myParents, -1);
    determine([...mySpouses, ...mySiblings], 0);
  }
};

const determineYPositions = (list, unions) => {
  determineYPosition(list[0], list, unions, 0);
};

export default determineYPositions;
