import _ from "lodash";

const findAllUnions = (persons) => {
  const unionIds = [
    ...new Set(
      persons.reduce((arr, person) => {
        if (person.p.length !== 2) return arr;
        arr.push(person.p.sort().join("@@@"));
        return arr;
      }, [])
    ),
  ].sort();

  const unions = unionIds.map((unionId) => {
    const p = unionId.split("@@@");

    const children = persons.filter((e) => _.isEqual(e.p, p)).map((e) => e.id);

    return {
      p,
      children,
    };
  });

  return {
    unionIds,
    unions,
  };
};

export default findAllUnions;
