import React from "react";
import styles from "./card.module.css";
import classnames from "classnames";

import { cardWidth, cardHeight } from "../../constants";

const Card = ({
  person,
  className,
  style = {
    width: cardWidth,
    height: cardHeight,
  },
  ...props
}) => {
  return (
    <div
      className={classnames(styles.container, styles[person.g], className)}
      style={style}
      {...props}
    >
      <div className={styles.name}>{person.name}</div>
    </div>
  );
};

export default Card;
