import { createTree } from "../utils/tree-utils";

const tree = createTree();
const { createPerson, createUnion, list } = tree;

const vlad = createPerson("Wife");
const john = createPerson("Father-In-Law");
const anna = createPerson("Mother-In-Law");
const papa = createPerson("Father");
const mama = createPerson("Mother");
const diana = createPerson("Me");
const grandpapa = createPerson("Grandfather");
const grandmama = createPerson("Grandmother");
const othergrandpapa = createPerson("Grandfather");
const othergrandmama = createPerson("Grandmother");

createUnion(papa, mama).attachChildren(diana, createPerson("Brother"));
createUnion(john, anna).attachChildren(vlad);
createUnion(vlad, diana).attachChildren(createPerson("Son"));
createUnion(othergrandpapa, othergrandmama).attachChildren(mama);
createUnion(grandpapa, grandmama).attachChildren(papa);

export default list;
