import uniqueString from "unique-string";

export const instantiatePerson = ({ name, p = [], g, treeId }) => ({
  name,
  id: uniqueString(),
  p,
  g,
});

export const createTree = (params = {}) => {
  const { name } = params;
  const treeId = uniqueString();

  const list = [];

  const createUnion = (person1, person2) => {
    return {
      attachChildren: (...children) => {
        children.forEach((child) => {
          child.p.push(person1.id, person2.id);
        });
        return [person1, person2];
      },
    };
  };

  const createPerson = (name) => {
    list.push(
      instantiatePerson({
        name,
        g: list.length % 2 === 0 ? "1" : "0",
      })
    );

    return list[list.length - 1];
  };

  return {
    createPerson,
    list,
    createUnion,
    treeId,
    name,
  };
};

export const optimizeTree = (tree) => {
  const list = [...tree.list];

  list.sort((a, b) => a.id.localeCompare(b.id));

  const cleanList = list.map((item) => {
    const cleanItem = { ...item, p: item.p };
    delete cleanItem.treeId;
    delete cleanItem.parents;

    return cleanItem;
  });

  const ids = list.map((e) => e.id);
  let replaced = JSON.stringify(cleanList);
  let count = 0;
  ids.forEach((id, i) => {
    replaced = replaced.replace(
      new RegExp(`\"${id}\"`, "g"),
      `"%${(count++).toString(36)}"`
    );
  });

  const cleanTree = {
    ...tree,
    list: JSON.parse(replaced),
  };

  // delete cleanTree.treeId;

  return cleanTree;
};
