import {
  cardWidth,
  cardHeight,
  cardSpacingVertical,
  cardSpacingHorizontal,
} from "../../constants";
import findPersonById from "../find-person-by-id";

export default (persons, unions) => {
  const lines = prelimenaryPlotting(persons, unions);

  spaceOverlapingLines(lines);

  continueLines(lines);

  spaceOverlapingLinesVertical(lines);

  return lines.map((line) => {
    return {
      assosiatedItems: line.assosiatedItems,
      points: line.points,
      type: line.params.type,
      id: line.id,
    };
  });
};

const prelimenaryPlotting = (persons, unions) => {
  const lines = [];

  const plotLine = (x1, y1, x2, y2, type, unionLine) => {
    let line;
    if (y1 === y2) {
      line = {
        points: [
          [x1, y1],
          [x2, y2],
        ],
        params: { x1, y1, x2, y2, type },
      };
    } else {
      const lineSpineY = y1 - (cardHeight / 2 + cardSpacingVertical / 2);
      line = {
        shouldBeContinued: true,
        points: [
          [x1, lineSpineY],
          [x2, lineSpineY],
        ],
        params: { x1, y1, x2, y2, type },
      };
    }

    line.points.sort(sortPoints);

    line.id = `line#${lines.length}`;
    line.unionLine = unionLine;

    lines.push(line);

    return line;
  };

  unions.forEach(({ p, children }) => {
    const [spouseId1, spouseId2] = p;
    const [spouse1, spouse2] = [
      findPersonById(spouseId1, persons),
      findPersonById(spouseId2, persons),
    ].sort((a, b) => {
      return a.x - b.x;
    });

    const [x1, y1] = calculateCardCenter(spouse1);
    const [x2, y2] = calculateCardCenter(spouse2);

    const [xUnion, yUnion] =
      y1 === y2
        ? [x1 + cardWidth / 2 + cardSpacingHorizontal / 2, y1]
        : getPointBetween(x1, y1, x2, y2);

    const unionLine = plotLine(x1, y1, x2, y2, "union");

    unionLine.assosiatedItems = [spouseId1, spouseId2, ...children];

    children.forEach((child) => {
      const [xChild, yChild] = calculateCardCenter(
        findPersonById(child, persons)
      );

      const childLine = plotLine(
        xChild,
        yChild,
        xUnion,
        yUnion,
        "child",
        unionLine
      );

      childLine.assosiatedItems = [spouseId1, spouseId2, child];
    });
  });

  return lines;
};

const getPointBetween = (x1, y1, x2, y2) => {
  const deltaX = x2 - x1;
  const deltaY = y2 - y1;
  return [x1 + deltaX / 2, y1 + deltaY / 2];
};

const calculateCardCenter = (person) => {
  const { x, y } = person;
  return [
    (cardWidth + cardSpacingHorizontal) * x + 0.5 * cardWidth,
    (cardHeight + cardSpacingVertical) * y + 0.5 * cardHeight,
  ];
};

const spaceOverlapingLines = (lines) => {
  const groups = [];

  const lineIsInGroup = (line) =>
    !!groups.find((group) => !!group.find((l) => l.id === line.id));

  lines.forEach((line) => {
    const hasBeenAddedToGroup = lineIsInGroup(line);
    if (hasBeenAddedToGroup) return;
    const group = [line];
    groups.push(group);

    lines.forEach((line2) => {
      if (
        !lineIsInGroup(line2) &&
        line2.points[0][1] === line.points[0][1] && // isnt same level
        line2.points[1][0] > line.points[0][0] && // isnt reaching
        line2.points[0][0] < line.points[1][0] // overreached
      ) {
        group.push(line2);
      }
    });
  });

  groups.forEach((group) => {
    const lineCount = group.length;
    const lineSpacing = Math.min(
      (cardSpacingVertical * 0.9) / lineCount,
      cardSpacingVertical / 3
    );
    const totalWidth = (lineCount - 1) * lineSpacing;

    group.sort((a, b) => {
      return a.points[0][0] - b.points[0][0];
    });

    group.forEach((line, i) => {
      line.points.forEach((point) => {
        point[1] += i * lineSpacing - totalWidth / 2;
      });
    });
  });
};
const spaceOverlapingLinesVertical = (lines) => {
  const groups = [];

  const verticalLines = [];

  lines.map((line) => {
    if (!line.shouldBeContinued) return;

    const myLines = [
      [line.points[0], line.points[1]],
      [line.points[2], line.points[3]],
    ];

    myLines.forEach((l, i) => {
      l.sort(sortPointsVertical);

      const [start, end] = l;

      verticalLines.push({
        id: line.id + "_" + i,
        start,
        end,
        sort: Math.max(line.points[0][0], line.points[3][0]),
      });
    });
  });

  const isInGroup = (line) =>
    !!groups.find((group) => !!group.find((l) => l.id === line.id));

  verticalLines.forEach((line) => {
    const hasBeenAddedToGroup = isInGroup(line);
    if (hasBeenAddedToGroup) return;

    const group = [line];
    groups.push(group);

    verticalLines.forEach((line2) => {
      if (
        !isInGroup(line2) &&
        line2.start[0] === line.start[0] && // is same level
        line2.start[1] < line.end[1] && // is reaching
        line2.end[1] > line.start[1] // has reaching
      ) {
        group.push(line2);
      }
    });
  });

  groups.forEach((group) => {
    group.sort((line, b) => line.sort - b.sort);
    const lineCount = group.length;
    const lineSpacing = (cardSpacingVertical * 0.9) / lineCount;
    const totalWidth = (lineCount - 1) * lineSpacing;
    group.forEach((line, i) => {
      line.start[0] += i * lineSpacing - totalWidth / 2;
      line.end[0] += i * lineSpacing - totalWidth / 2;
    });
  });
};

const continueLines = (lines) => {
  lines
    .filter((l) => l.shouldBeContinued)
    .forEach((line) => {
      let { unionLine } = line;

      if (unionLine) {
        line.params.y2 = unionLine.points[0][1];
      }

      const pointsGoals = [
        [line.params.x1, line.params.y1],
        [line.params.x2, line.params.y2],
      ].sort(sortPoints);

      const points = line.points.sort(sortPoints);

      line.points = [pointsGoals[0], ...points, pointsGoals[1]];
    });
};

const sortPoints = (a, b) => {
  return a[0] - b[0];
};

const sortPointsVertical = (a, b) => {
  return a[1] - b[1];
};
