import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import PageMain from "./pages/Main";
import PageManageTrees from "./pages/ManageTrees";
import PageTree from "./pages/TreePage";

const App = () => {
  return (
    <Router>
      <Switch>
        <Route path="/tree/:treeId" component={PageTree} />
        <Route path="/manage-trees" component={PageManageTrees} />
        <Route path="/" component={PageMain} />
      </Switch>
    </Router>
  );
};

export default App;
