import _ from "lodash";
import plotPersons from "./plot-persons";
import findAllUnions from "./find-all-unions";
import getDimensions from "./get-dimensions";
import plotLines from "./plot-lines";
import {
  cardSpacingHorizontal,
  cardHeight,
  cardWidth,
  cardSpacingVertical,
} from "../constants";

export default (originalList, params = {}) => {
  const { shouldShrink = false } = params;

  const timeStart = performance.now();

  const clonedList = _.cloneDeep(originalList);

  fixMissingParents(clonedList);

  const { unions } = findAllUnions(clonedList);

  const list = plotPersons(clonedList, unions, shouldShrink);

  const { rows, columns } = getDimensions(list);

  const lines = plotLines(list, unions);

  const timeEnd = performance.now();

  const time = timeEnd - timeStart;

  const processed = {
    list,
    unions,
    rows,
    columns,
    lines,
    time,
    treeWidth: columns * cardWidth + (columns - 1) * cardSpacingHorizontal,
    treeHeight: rows * cardHeight + (rows - 1) * cardSpacingVertical,
  };

  console.log({ processed }, processed.time);

  return processed;
};

const fixMissingParents = (list) => {
  const ids = list.map(({ id }) => id);
  list.forEach((item) => {
    if (item.p && item.p.length > 0) {
      item.p.forEach((parentId) => {
        if (ids.indexOf(parentId) === -1) {
          item.p = [];
        }
      });
    }
  });
};
