import trimPersons from "../trim-persons";
import findPersonById from "../find-person-by-id";
import findPersonByCoordinates from "../find-person-by-coordinates";
import determineYPositions from "../determine-y-positions";
import _ from "lodash";
import map from "../../utils/map";
import getDimensions from "../get-dimensions";
import {
  cardWidth,
  cardHeight,
  cardSpacingVertical,
  cardSpacingHorizontal,
} from "../../constants";

const whileTrue = (cb, limit = 200) => {
  let count = 0;
  while (cb()) {
    count++;
    if (count === limit) break;
  }
};

export default (persons, unions = [], shouldShrink) => {
  persons.sort(byId);

  determineYPositions(persons, unions);
  trimPersons(persons);

  persons.forEach((person, i) => (person.x = i * 1));

  unions.forEach((u) => (u.y = findPersonById(u.p[0], persons).y));

  for (let i = 0; i < 2; i++) {
    stickTogether(unions, persons);
    childrenUnderParents(unions, persons);
    parentsOverChildren(unions, persons);
    pop(persons, unions);
    popSingle(persons);
    whileTrue(() => fixUnionIntruders(persons, unions) > 0);
    trimGaps(persons);
    trimPersons(persons);
    if (shouldShrink) shrink(persons);
  }

  persons.forEach((person) => {
    person.dimensions = {
      x: person.y * (cardHeight + cardSpacingHorizontal),
      y: person.x * (cardWidth + cardSpacingVertical),
      width: cardWidth,
      height: cardHeight,
    };
  });

  return persons;
};

const stickTogether = (unions, persons) => {
  unions.forEach(({ children }) => moveCloser(children, persons));
  unions.forEach(({ p }) => moveCloser(p, persons));
};

const parentsOverChildren = (unions, persons) => {
  unions.sort((a, b) => b.y - a.y);
  unions.forEach(({ p, children }) => {
    if (children.length === 0) return;

    const parentsMiddle = getGroupCenterX(p, persons);
    const childrenMiddle = getGroupCenterX(children, persons);

    moveX(p, childrenMiddle - parentsMiddle, persons);
  });
};

const childrenUnderParents = (unions, persons) => {
  unions.sort((a, b) => b.y - a.y);
  unions.forEach(({ p, children }) => {
    if (children.length === 0) return;
    const parentsMiddle = getGroupCenterX(p, persons);
    const childrenMiddle = getGroupCenterX(children, persons);
    moveX(children, -(childrenMiddle - parentsMiddle), persons);
  });
};

const fixUnionIntruders = (persons, unions = []) => {
  const calculateOverlap = ({ p }) => {
    const unionMembers = p
      .map((id) => findPersonById(id, persons))
      .sort((a, b) => a.x - b.x);
    const unionY = unionMembers[0].y;
    const [memberLeft, memberRight] = unionMembers;
    const difference = memberRight.x - memberLeft.x;
    if (difference <= 0) return;
    const intruders = new Array(difference - 1)
      .fill()
      .map((_, i) =>
        findPersonByCoordinates(memberLeft.x + i + 1, unionY, persons)
      )
      .filter((e) => !!e);

    return {
      intruders,
      memberLeft,
      memberRight,
      p,
      hasOverlap: intruders.length > 0,
    };
  };

  const getGlobalOverlaps = () =>
    unions
      .map(calculateOverlap)
      .filter((e) => !!e && e.intruders.length > 0)
      .sort((a, b) => {
        return a.difference - b.difference;
      });

  let overlaps = getGlobalOverlaps();

  const fixOverlap = ({ intruders, memberLeft, memberRight, p }) => {
    if (calculateOverlap({ p }).hasOverlap) {
      for (const intruder of intruders) {
        const temp1 = memberLeft.x;
        const temp2 = intruder.x;

        memberLeft.x = temp2;
        intruder.x = temp1;

        if (calculateOverlap({ p }).hasOverlap) {
          memberLeft.x = temp1;
          intruder.x = temp2;
        } else {
          break;
        }
      }
    }
  };

  overlaps.forEach((args) => {
    fixOverlap(args);
    fixOverlap({ ...args, intruders: [...args.intruders].reverse() });
  });

  overlaps = getGlobalOverlaps();

  return overlaps.length;
};

const moveCloser = (ids, persons) => {
  const movablePersons = ids.map((id) => findPersonById(id, persons));
  for (let i = 1; i < movablePersons.length; i++) {
    const prev = movablePersons[i - 1];
    const curr = movablePersons[i];
    curr.x = prev.x + 1;
  }
};

const moveX = (ids, delta, persons) => {
  const movablePersons = ids.map((id) => findPersonById(id, persons));
  for (let i = 0; i < movablePersons.length; i++) {
    movablePersons[i].x += delta;
  }
};

const getGroupCenterX = (ids, persons) => {
  const y = findPersonById(ids[0], persons).y;
  const xs = ids.map((id) => findPersonById(id, persons).x);

  const left = _.min(xs);
  const right = _.max(xs);

  const ans = (left + right) / 2;

  return y % 2 > 1 ? Math.round(ans) : Math.floor(ans);
};

const byId = (a, b) => `${a}`.localeCompare(b);

const trimGaps = (persons) => {
  let x = 0;
  while (true) {
    const personsBeyond = persons.filter((person) => x < person.x);
    if (personsBeyond.length === 0) break;
    const lineIsEmpty = persons.filter((person) => person.x === x).length;
    if (lineIsEmpty === 0) {
      personsBeyond.forEach((person) => person.x--);
    } else x++;
  }
  return persons;
};

const pop = (persons, unions) => {
  const { rows, columns } = getDimensions(persons);

  const findSpousesAndSiblings = (person) => {
    const myUnions = unions.filter(({ p }) => p.indexOf(person.id) !== -1);
    const spouseIds = myUnions.reduce((arr, { p }) => {
      const spouseId = p[0] === person.id ? p[1] : p[0];
      arr.push(spouseId);
      return arr;
    }, []);
    const siblingIds = persons.reduce((arr, { p, id }) => {
      if (person.p.length !== 0 && _.isEqual(person.p.sort(), p.sort()))
        arr.push(id);
      return arr;
    }, []);
    return [...spouseIds, ...siblingIds].map((id) =>
      findPersonById(id, persons)
    );
  };

  map(rows, (row) => {
    map(columns, (i) => {
      const column = columns - i - 1;
      const personsRow = persons.filter(({ y }) => y === row);
      const person = findPersonByCoordinates(column, row, personsRow);

      if (!person) return;

      const spousesAndSiblings = findSpousesAndSiblings(person);
      const leftSpousesAndSiblings = spousesAndSiblings.filter(
        ({ x }) => x < column
      );
      whileTrue(() =>
        fixOverlaps([person, ...leftSpousesAndSiblings], personsRow)
      );
    });
  });
};

const fixOverlaps = (myPersons, persons) => {
  let has = false;
  for (const a of myPersons) {
    for (const b of persons) {
      if (b.id !== a.id && b.x === a.x && b.y === a.y) {
        const sibling = b.p.length !== 0 && _.isEqual(b.p.sort(), a.p.sort());
        if (sibling)
          myPersons.filter((e) => e.id !== a.id).map((p) => (p.x += 1));
        else myPersons.map((p) => (p.x += 1));
        has = true;
      }
    }
  }
  return has;
};

const getOverlaps = (person, persons) => {
  return persons.filter((e) => {
    if (e.id === person.id || e.y !== person.y || e.x !== person.x)
      return false;
    return true;
  });
};

const popSingle = (persons) => {
  persons.forEach((person) => {
    whileTrue(() => {
      const condition = getOverlaps(person, persons).length > 0;
      if (condition) person.x++;
      return condition;
    });
  });
};

const shrink = (persons) => {
  const { rows, columns } = getDimensions(persons);
  map(rows, (row) => {
    map(columns, (column) => {
      const x = columns - column - 1;
      if (!findPersonByCoordinates(x, row, persons)) {
        persons.filter((e) => e.y === row && e.x > x).forEach((e) => e.x--);
      }
    });
  });
};
