import React from "react";
import { Link, useParams } from "react-router-dom";
import { useStatePersist } from "use-state-persist";
import Tree from "../../components/Tree";
import classnames from "classnames";
import "./tree-page.scss";
import { instantiatePerson, optimizeTree } from "../../utils/tree-utils";
import ManageTrees from "../ManageTrees";

function TreePage({}) {
  const { treeId } = useParams();

  const [updateKey, setUpdateKey] = React.useState(0);
  const [zoom = 1, setZoom] = useStatePersist(`@tree-zoom`);
  const [trees, setTrees] = useStatePersist("@trees");
  const [shouldShrink = false, setShouldShrink] = useStatePersist(
    `@trees-shrink${treeId}`
  );
  const [selectedItems, setSelectedItems] = React.useState([]);

  const [treeDimensions, setTreeDimensions] = React.useState([10000, 10000]);

  const tree = trees.find((t) => t.treeId === treeId);

  if (!tree) return <ManageTrees></ManageTrees>;

  const { name } = tree;

  const getPerson = (id) => tree.list.find((item) => item.id === id);

  const itemHasParents = (id) => {
    const item = getPerson(id);
    if (item && item.p.length === 2) return true;
    return false;
  };

  const pushToList = (...persons) => {
    setTrees([
      ...trees.filter((tree) => tree.treeId !== treeId),
      optimizeTree({
        ...tree,
        edited: true,
        list: [
          ...tree.list.filter((item) => !persons.find((p) => p.id === item.id)),
          ...persons,
        ],
      }),
    ]);
  };

  if (tree.list.length === 0) {
    setTrees([...trees.filter((tree) => tree.treeId !== treeId)]);
    return null;
  }

  const editSelectedItem = () => {
    if (selectedItems.length !== 1) return;
    const [id] = selectedItems;
    const person = getPerson(id);

    const name = prompt("Please enter person name", person.name);

    if (!name) return;

    pushToList({
      ...person,
      name: name,
    });
    setUpdateKey(updateKey + 1);
    setSelectedItems([]);
  };

  return (
    <div
      className="tree-view-page"
      tabIndex="0"
      onKeyDown={(e) => {
        if (e.keyCode === 13) {
          editSelectedItem();
        }
      }}
    >
      <div className="flex tree-view-page-header">
        <div className="flex">
          <h2>{name}</h2>
          <Link className="button" to="/manage-trees">
            home
          </Link>
          <div
            className={classnames("button minimal-button", {
              disabled: zoom >= 2,
            })}
            onClick={() => setZoom(zoom + 0.2)}
          >
            Zoom +
          </div>
          <div
            className={classnames("button minimal-button", {
              disabled: zoom <= 0.2,
            })}
            onClick={() => setZoom(zoom - 0.2)}
          >
            Zoom -
          </div>
          <div
            className={classnames("button minimal-button", {
              disabled: selectedItems.length !== 1,
            })}
            onClick={() => {
              const [id] = selectedItems;
              const item = getPerson(id);
              const partner = instantiatePerson({
                treeId,
              });
              const child = instantiatePerson({
                treeId,
                p: [id, partner.id],
              });
              pushToList(partner, child);
              setSelectedItems([]);
            }}
          >
            add partner
          </div>
          <div
            className={classnames("button minimal-button", {
              disabled:
                selectedItems.length !== 1 || itemHasParents(selectedItems[0]),
            })}
            onClick={() => {
              const [id] = selectedItems;
              const item = getPerson(id);

              const parent1 = instantiatePerson({
                treeId,
              });

              const parent2 = instantiatePerson({
                treeId,
              });

              pushToList(
                {
                  ...item,
                  p: [parent1.id, parent2.id],
                },
                parent1,
                parent2
              );

              setSelectedItems([]);
            }}
          >
            add parents
          </div>

          <div
            className={classnames("button minimal-button", {
              disabled: selectedItems.length !== 1,
            })}
            onClick={editSelectedItem}
          >
            Edit item
          </div>

          <div
            className={classnames("button minimal-button", {
              disabled: selectedItems.length === 0,
            })}
            onClick={() => {
              setTrees([
                ...trees.filter((tree) => tree.treeId !== treeId),
                {
                  ...tree,
                  list: [
                    ...tree.list.filter(
                      (item) => !selectedItems.find((id) => id === item.id)
                    ),
                  ],
                },
              ]);
              setSelectedItems([]);
            }}
          >
            Delete
          </div>

          <div
            className={classnames("button minimal-button", {
              disabled: selectedItems.length !== 2,
            })}
            onClick={() => {
              const [parent1, parent2] = selectedItems;

              pushToList(
                instantiatePerson({
                  treeId,
                  p: [parent1, parent2],
                })
              );
            }}
          >
            add child
          </div>

          <div
            className={classnames("button minimal-button", {
              // disabled: selectedItems.length !== 1,
            })}
            onClick={() => {
              setShouldShrink(!shouldShrink);
            }}
          >
            Shrink {shouldShrink ? "+" : "-"}
          </div>
        </div>
      </div>
      <hr />

      <div className="tree-display-container">
        <div
          style={{
            width: treeDimensions[0] * zoom + 20,
            height: treeDimensions[1] * zoom + 20,
            padding: 20,
          }}
        >
          <div
            style={{
              transform: `scale(${zoom})`,
            }}
          >
            <Tree
              updateKey={updateKey}
              shouldShrink={shouldShrink}
              selectedItems={selectedItems}
              persons={tree.list}
              onCalculatedDimensions={([w, h]) => {
                if (w !== treeDimensions[0] || h !== treeDimensions[1]) {
                  setTreeDimensions([w, h]);
                }
              }}
              onClickPerson={({ id }) => {
                const isSelected = selectedItems.indexOf(id) !== -1;
                setSelectedItems(
                  isSelected
                    ? selectedItems.filter((e) => e !== id)
                    : [...selectedItems, id]
                );
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default TreePage;
