import React from "react";
import { useStatePersist } from "use-state-persist";
import { createTree, optimizeTree } from "../../utils/tree-utils";

import { Link } from "react-router-dom";
import { stringify } from "zipson";
const prettyBytes = require("pretty-bytes");

function downloadFile(filename, text) {
  var element = document.createElement("a");
  element.setAttribute(
    "href",
    "data:text/plain;charset=utf-8," + encodeURIComponent(text)
  );
  element.setAttribute("download", filename);
  element.style.display = "none";
  document.body.appendChild(element);
  element.click();
  document.body.removeChild(element);
}

const TreeList = () => {
  const [trees, setTrees] = useStatePersist("@trees");

  React.useEffect(() => {
    if (!trees) {
      setTrees([]);
    }
  }, [!!trees]);

  if (!trees) return null;

  const addTreeToMyList = (e) => {
    // e.preventDefault();
    const name = prompt("Please enter tree name", `Tree ${Date.now()}`);

    if (!name) return;

    const { list, treeId, createUnion, createPerson } = createTree({ name });

    createUnion(
      createPerson("Person 1"),
      createPerson("Person 2")
    ).attachChildren(createPerson("Person 3"));

    setTrees([
      ...trees,
      {
        list,
        treeId,
        name,
        edited: false,
      },
    ]);
  };

  const removeTree = (treeId) => () => {
    if (window.confirm("Are you sure you want to delete?")) {
      setTrees(trees.filter((tree) => tree.treeId !== treeId));
    }
  };

  const renderMyTreesList = () => {
    return (
      <table>
        <thead></thead>
        <tbody>
          {trees.map((tree) => {
            const { treeId, name, list, edited } = tree;

            const str = stringify(tree);

            const exportTree = () => {
              downloadFile(`Tree--${name}.txt`, str);
            };

            return (
              <React.Fragment key={treeId}>
                <tr>
                  <th>
                    <span className="tree-name">{name}</span>
                  </th>
                  <th>
                    {edited ? (
                      `${list.length} people`
                    ) : (
                      <span className="new-tree-label">New</span>
                    )}
                  </th>
                  <th>
                    <button
                      onClick={removeTree(treeId)}
                      className="delete-button"
                    >
                      <img
                        src="/delete.png"
                        alt=""
                        width="20px"
                        height="20px"
                      />
                    </button>
                  </th>
                  <th>
                    <button onClick={exportTree}>
                      Export{" "}
                      <span className="small-text">
                        ({prettyBytes(new TextEncoder().encode(str).length)})
                      </span>
                    </button>
                  </th>

                  <th>
                    <Link to={`/tree/${treeId}`} className="button">
                      view
                    </Link>
                  </th>
                </tr>
              </React.Fragment>
            );
          })}
        </tbody>
      </table>
    );
  };

  return (
    <div className="my-tree-list">
      <h3>My Trees</h3>

      <div className="my-tree-list-body">
        {trees.length === 0 ? (
          <span className="no-trees-message">You don't have any trees yet</span>
        ) : (
          renderMyTreesList()
        )}
      </div>

      <button onClick={addTreeToMyList}>Create a tree</button>
    </div>
  );
};

export default TreeList;
