import React, { useMemo, useState, useEffect } from "react";
import classnames from "classnames";

import Card from "../Card";
import Line from "../Line";

import processor from "../../processor";
import styles from "./tree.module.css";
import stylesCard from "../Card/card.module.css";

const Tree = ({
  updateKey = 1,
  persons = [],
  forceSelectedPersonId,
  onCalculatedDimensions,
  onClickPerson,
  selectedItems = [],
  shouldShrink = false,
}) => {
  const hasForcedId = forceSelectedPersonId;

  const { list, lines, treeWidth, treeHeight } = useMemo(
    () => processor(persons, { shouldShrink }),
    [
      persons
        .map((e) => e.id)
        .sort()
        .join(""),
      shouldShrink,
      updateKey,
    ]
  );

  React.useEffect(() => {
    if (onCalculatedDimensions) {
      onCalculatedDimensions([treeWidth, treeHeight]);
    }
  });

  const [hoverPerson, setHoverPerson] = useState(null);
  const elementId = useMemo(() => generateID(), []);

  const selectedPerson = forceSelectedPersonId
    ? list.find(({ id }) => id === forceSelectedPersonId)
    : hoverPerson;

  useEffect(() => {
    if (
      selectedPerson &&
      forceSelectedPersonId &&
      hoverPerson &&
      hoverPerson.id !== forceSelectedPersonId
    ) {
      setHoverPerson(null);
    }
  }, [forceSelectedPersonId]);

  const mouseEnter = (e) => {
    const person = persons.find(
      (p) => p.id === e.target.getAttribute("data-person-id")
    );
    if (person && person !== selectedPerson) setHoverPerson(person);
  };

  const resetSelectedPerson = (e) => {
    if (selectedPerson) setHoverPerson(null);
  };

  const cardOnClick = onClickPerson
    ? (e) =>
        onClickPerson(
          persons.find((p) => p.id === e.target.getAttribute("data-person-id"))
        )
    : null;

  const cards = list.map((person) => (
    <Card
      onClick={cardOnClick}
      onMouseLeave={!hasForcedId ? resetSelectedPerson : null}
      data-person-id={person.id}
      onMouseEnter={!hasForcedId ? mouseEnter : null}
      key={person.id}
      person={person}
      className={classnames({
        [stylesCard.selected]:
          selectedPerson && person.id === selectedPerson.id,
        [stylesCard.highlight]: isPersonHighlighted(person, selectedPerson),
        [stylesCard.selected]: selectedItems.indexOf(person.id) !== -1,
      })}
      style={{
        position: "absolute",
        top: person.dimensions.x,
        left: person.dimensions.y,
        width: person.dimensions.width,
        height: person.dimensions.height,
      }}
    />
  ));

  const connecters = lines.map(({ points, type, id, assosiatedItems }) => (
    <Line
      key={id}
      type={type}
      isHighlighted={
        selectedPerson && assosiatedItems.indexOf(selectedPerson.id) !== -1
      }
    >
      <path fill="none" d={["M", ...points].join(" ")} />
    </Line>
  ));

  return (
    <div
      id={elementId}
      className={styles.container}
      style={{
        width: treeWidth,
        height: treeHeight,
      }}
    >
      {connecters}
      {cards}
    </div>
  );
};

export default Tree;

const isPersonHighlighted = (person, selectedPerson) => {
  if (!selectedPerson) return false;
  if (person.p.indexOf(selectedPerson.id) !== -1) return true;
  if (selectedPerson.p.indexOf(person.id) === -1) return false;
  return true;
};

function generateID() {
  let result = "";
  const characters = "abcdefghijklmnopqrstuvwxyz";
  for (let i = 0; i < 6; i++) {
    const index = Math.floor(Math.random() * characters.length);
    result += characters.charAt(index);
  }
  return "tree-" + result;
}
